* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  overflow: hidden;
}

.video-container {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

video {
  object-fit: cover;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.centered-box {
  text-align: center;
  background-color: #374151;
  border: 2px solid #fff;
  border-radius: 15px;
  width: 90%;
  max-width: 500px;
  padding: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px #0003;
}

.centered-box h2 {
  color: #fff;
  margin-bottom: 20px;
  font-family: Arial, sans-serif;
}

.form-field {
  background-color: #0000;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-wrap: nowrap;
  align-items: center;
  gap: 8px;
  margin: 20px 0;
  padding: 6px 8px;
  display: flex;
  overflow: hidden;
}

.email-icon {
  flex-shrink: 0;
  width: 16px;
  height: 16px;
}

.email-input {
  color: #6b7280;
  background-color: #0000;
  border: none;
  outline: none;
  flex: 1;
  min-width: 0;
  font-family: Arial, sans-serif;
  font-size: 14px;
}

.submit-button {
  color: green;
  cursor: pointer;
  text-align: left;
  background: none;
  border: none;
  margin-left: -5px;
  padding: 0 6px;
  font-family: Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  transition: color .3s;
}

.submit-button:hover {
  color: #006400;
}

.divider {
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  display: flex;
}

.divider .line {
  background-color: #ccc;
  flex: 1;
  height: 1px;
}

.divider .or-text {
  color: #fff;
  margin: 0 10px;
  font-family: Arial, sans-serif;
  font-size: 14px;
}

.phantom-button {
  color: #000;
  cursor: pointer;
  background-color: #d3d3d3;
  border: none;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .3s;
  display: inline-flex;
  box-shadow: 0 2px 4px #0003;
}

.phantom-button:hover {
  background-color: #e0e0e0;
}

.phantom-icon {
  width: 20px;
  height: 20px;
}

@media (width <= 600px) {
  .form-field {
    padding: 4px 6px;
  }

  .submit-button {
    margin-left: -8px;
    font-size: 10px;
  }

  .email-input {
    font-size: 12px;
  }
}

@media (width <= 400px) {
  .form-field {
    padding: 4px 6px;
  }

  .submit-button {
    margin-left: -8px;
    font-size: 10px;
  }

  .email-input {
    font-size: 12px;
  }
}
